*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #ccc;
  background-color: #111;
  font-family: Montserrat, sans-serif;
  overflow: hidden;
}

a, a:visited {
  color: #facf70;
  text-decoration: none;
}

a:hover, a:visited:hover {
  text-decoration: underline;
}

.header {
  color: #111;
  width: 100%;
  height: 100%;
  background-color: #facf70;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding-right: 1rem;
  font-size: 1.2rem;
  display: flex;
}

.header #openSidenav {
  background-color: inherit;
  cursor: pointer;
  font-family: inherit;
  font-size: 1.6rem;
  font-size: inherit;
  text-align: center;
  height: 100%;
  border-width: 0;
  border-radius: 0;
  margin: 0 0 0 .5rem;
  padding: .5rem;
  display: inline-block;
}

.header #openSidenav:hover {
  color: #facf70;
  background-color: #111;
}

.sidenav {
  height: 100%;
  width: 0;
  z-index: 1;
  background-color: #111;
  padding-top: 3rem;
  transition: all .15s;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
}

.sidenav a {
  color: #ccc;
  padding: .4rem .4rem .4rem 2rem;
  font-size: 1.25rem;
  text-decoration: none;
  transition: all .1s;
  display: block;
}

.sidenav a:hover {
  color: #facf70;
}

.sidenav .closebtn {
  margin-left: 50px;
  font-size: 36px;
  position: absolute;
  top: 0;
  right: 25px;
}

.sidenav .closebtn:hover {
  color: #facf70;
}

@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 15px;
  }

  .sidenav a {
    font-size: 18px;
  }
}

.container {
  height: 100vh;
  grid-template: "header" 2.5rem
                 "main"
                 "options" 2rem
                 "footer" 2rem
                 / auto;
  justify-items: center;
  transition: all .5s;
  display: grid;
  position: absolute;
  inset: 0;
}

.unselectable {
  -moz-user-select: -moz-none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  grid-area: header;
}

.options {
  flex-direction: row;
  grid-area: options;
  justify-content: flex-end;
  display: flex;
}

#main {
  grid-area: main;
  padding-top: 2rem;
}

.credits {
  width: 98%;
  text-align: justify;
  grid-area: footer;
  justify-content: space-between;
  display: flex;
  position: absolute;
  bottom: 1rem;
}

.button-2 {
  cursor: pointer;
  font-family: inherit;
  font-size: inherit;
  text-align: center;
  vertical-align: baseline;
  white-space: nowrap;
  -webkit-user-select: none;
  user-select: none;
  touch-action: manipulation;
  background-color: #facf70;
  border-width: 0;
  border-radius: 1px;
  justify-self: end;
  margin: 0;
  padding: .5rem;
  list-style: none;
  transition: all .2s;
  display: inline-block;
}

.button-2:active {
  color: #facf70;
  background-color: #111;
}

::-webkit-scrollbar {
  display: none;
}

